.login-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #e65100 0%, #ff6b35 50%, #ff8746 100%);
  position: relative;
  overflow: hidden;
  transition: background 0.5s ease;

  // 다크 모드 배경
  &.dark-mode {
    background: linear-gradient(135deg, #1a1a2e 0%, #16213e 50%, #0f3460 100%);

    .login-card {
      background: #1e1e2e;
      border: 1px solid rgba(255, 255, 255, 0.1);
      box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5), 
                  0 8px 32px rgba(0, 0, 0, 0.3);

      .login-form {
        background: #1e1e2e;
        color: #e0e0e0;

        .form-field {
          .full-width {
            ::ng-deep .mat-mdc-text-field-wrapper {
              background-color: #2a2a3a !important;
            }

            ::ng-deep .mat-mdc-form-field-label {
              color: #b0b0c0 !important;
            }

            ::ng-deep .mat-mdc-form-field-input {
              color: #ffffff !important;
            }

            ::ng-deep .mdc-text-field--filled:not(.mdc-text-field--disabled) {
              background-color: #2a2a3a !important;
            }

            ::ng-deep .mat-mdc-form-field-focus-overlay {
              background-color: transparent !important;
            }

            // 비밀번호 표시 아이콘
            ::ng-deep .mat-mdc-icon-button {
              color: #b0b0c0 !important;
            }

            // 입력 필드 하단 라인
            ::ng-deep .mdc-line-ripple::after {
              border-bottom-color: #90caf9 !important;
            }

            // 에러 텍스트
            ::ng-deep .mat-mdc-form-field-error {
              color: #ff6b6b !important;
            }

            // 힌트 텍스트
            ::ng-deep .mat-mdc-form-field-hint {
              color: #b0b0c0 !important;
            }
          }
        }

        .error-message {
          background: rgba(255, 107, 107, 0.15) !important;
          color: #ff6b6b !important;
          border: 1px solid rgba(255, 107, 107, 0.3);

          mat-icon {
            color: #ff6b6b !important;
          }
        }
      }

      .links {
        background: #1e1e2e;

        .link-row {
          .link-item {
            color: #b0b0c0;

            a {
              color: #90caf9 !important;

              &:hover {
                color: #64b5f6 !important;
              }
            }

            mat-icon {
              color: #90caf9 !important;
            }
          }

          .separator {
            color: #505060;
          }
        }
      }

      .divider {
        background: #1e1e2e;

        .divider-line {
          background: #3a3a4a;
        }

        .divider-text {
          background: #1e1e2e;
          color: #808090;
        }
      }

      .register-section {
        background: #2a2a3a;
        border-top: 1px solid rgba(255, 255, 255, 0.1);

        p {
          color: #b0b0c0;
        }

        .register-link {
          color: #90caf9 !important;

          &:hover {
            color: #64b5f6 !important;
          }
        }
      }
    }
  }

  // 테마 토글 버튼
  .theme-toggle {
    position: absolute;
    top: 20px;
    right: 20px;
    z-index: 10;
    background: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.3);
    color: white;
    width: 48px;
    height: 48px;
    transition: all 0.3s ease;

    &:hover {
      background: rgba(255, 255, 255, 0.3);
      transform: scale(1.1) rotate(15deg);
    }

    mat-icon {
      font-size: 24px;
      width: 24px;
      height: 24px;
    }
  }

  .login-card {
    width: 100%;
    max-width: 460px;
    margin: 20px;
    background: var(--bg-primary);
    border-radius: 16px;
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.2), 
                0 8px 32px rgba(229, 81, 0, 0.4);
    overflow: hidden;
    z-index: 2;
    position: relative;
    border: 1px solid rgba(255, 107, 53, 0.2);

    .login-header {
      background: linear-gradient(135deg, var(--primary-color), var(--primary-dark));
      color: white;
      padding: 24px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      position: relative;

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: linear-gradient(45deg, transparent 30%, rgba(255, 255, 255, 0.1) 50%, transparent 70%);
        animation: shimmer 3s infinite;
      }

      .mode-indicator {
        display: flex;
        align-items: center;
        gap: 12px;
        z-index: 1;

        mat-icon {
          font-size: 28px;
          width: 28px;
          height: 28px;
          filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.3));
        }

        h2 {
          margin: 0;
          font-size: 20px;
          font-weight: 600;
          text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
        }
      }

      .mode-switcher {
        z-index: 1;

        .mode-switch-btn {
          color: white;
          border: 1px solid rgba(255, 255, 255, 0.4);
          border-radius: 8px;
          padding: 8px 12px;
          font-size: 12px;
          backdrop-filter: blur(10px);

          &:hover {
            background: rgba(255, 255, 255, 0.15);
          }
        }
      }
    }

    .login-form {
      padding: 32px 24px 24px;
      background: var(--bg-primary);
      color: var(--text-primary);

      .form-field {
        margin-bottom: 20px;

        .full-width {
          width: 100%;

          ::ng-deep .mat-mdc-form-field-subscript-wrapper {
            margin-top: 4px;
          }

          ::ng-deep .mat-mdc-text-field-wrapper {
            background-color: var(--bg-secondary);
          }

          ::ng-deep .mat-mdc-form-field-label {
            color: var(--text-secondary);
          }

          ::ng-deep .mat-mdc-form-field-input {
            color: var(--text-primary);
          }

          ::ng-deep .mat-mdc-form-field-error {
            color: var(--error-color);
          }
        }
      }

      .error-message {
        display: flex;
        align-items: center;
        gap: 8px;
        background: var(--error-light);
        color: var(--error-color);
        padding: 12px 16px;
        border-radius: 8px;
        margin-bottom: 20px;
        font-size: 14px;

        mat-icon {
          font-size: 18px;
          width: 18px;
          height: 18px;
        }
      }

      .form-actions {
        margin-bottom: 24px;

        .login-btn {
          height: 48px;
          font-size: 16px;
          font-weight: 600;
          border-radius: 12px;
          display: flex;
          align-items: center;
          justify-content: center;
          gap: 8px;
          background: linear-gradient(135deg, var(--primary-color), var(--primary-dark));
          border: none;
          position: relative;
          overflow: hidden;
          transition: all 0.3s ease;
          
          &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background: linear-gradient(45deg, transparent 30%, rgba(255, 255, 255, 0.2) 50%, transparent 70%);
            transform: translateX(-100%);
            transition: transform 0.6s;
          }
          
          &:hover::before {
            transform: translateX(100%);
          }
          
          &:hover {
            transform: translateY(-2px);
            box-shadow: 0 8px 25px rgba(255, 107, 53, 0.4);
          }

          mat-spinner {
            ::ng-deep circle {
              stroke: white;
            }
          }
        }
      }

      .additional-links {
        text-align: center;
        color: var(--text-primary);

        .link-group {
          display: flex;
          align-items: center;
          justify-content: center;
          gap: 8px;
          margin-bottom: 16px;

          .divider {
            color: var(--text-secondary);
            font-size: 12px;
          }

          button {
            font-size: 13px;
            min-width: auto;
            padding: 4px 8px;
            color: var(--primary-color);
          }
        }

        .register-link {
          display: flex;
          align-items: center;
          justify-content: center;
          gap: 4px;
          font-size: 14px;
          color: var(--text-secondary);

          button {
            font-size: 14px;
            font-weight: 600;
            min-width: auto;
            padding: 4px 8px;
            color: var(--primary-color);
          }
        }
      }
    }
  }

  .background-decoration {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;

    .decoration-circle {
      position: absolute;
      border-radius: 50%;
      background: rgba(255, 255, 255, 0.1);

      &.circle-1 {
        width: 200px;
        height: 200px;
        top: -100px;
        right: -100px;
        animation: float 6s ease-in-out infinite;
      }

      &.circle-2 {
        width: 150px;
        height: 150px;
        bottom: -75px;
        left: -75px;
        animation: float 8s ease-in-out infinite reverse;
      }

      &.circle-3 {
        width: 100px;
        height: 100px;
        top: 50%;
        left: -50px;
        animation: float 10s ease-in-out infinite;
      }
    }
  }
}

@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-20px);
  }
}

// 반응형 디자인
@media (max-width: 480px) {
  .login-container {
    padding: 20px;

    .login-card {
      margin: 0;
      border-radius: 12px;

      .login-header {
        padding: 20px;

        .mode-indicator {
          h2 {
            font-size: 18px;
          }
        }
      }

      .login-form {
        padding: 24px 20px 20px;

        .form-field {
          margin-bottom: 16px;
        }
      }

      .social-login {
        padding: 0 20px 20px;
      }
    }
  }
}

// Shimmer animation
@keyframes shimmer {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}